.home_img img {
    height: 15rem;
}

.home_container {
    text-align: center;
    padding: 0rem;
    display: table-cell;
    width: 100vw;
    height: 100vh;
    vertical-align: middle;
}

.tiny {
    font-size: 0.7rem !important;
}

.muted_p {
    color: #7e7e7e;
    font-size: 0.95rem;
    margin: 0 1rem 1rem;
    line-height: 2;
    transition: all 0.25s ease;
    padding: 0 2rem;
}

.home_down_arrow {
    transform: rotate(180deg) translateY(0);
    width: 1rem;
    opacity: 0.8;
    margin: 0 auto;
    padding: 1rem 1rem;
    padding-bottom: 3rem;
}

.home_down_arrow:hover {
    transform: rotate(180deg) translateY(-5px);
}

.home_button {
    text-decoration: none;
    display: inline-block;
    border: 2px solid rgba(255, 255, 255, 0);
    padding: 0.5rem 1rem;
    margin: 1rem;
    font-size: 0.9rem;
    transition: all 0.25s ease;
    color: rgba(33, 33, 33, 0.74);
    width: 100px;
    border: 2px solid rgba(33, 33, 33, 0.74);
}

.home_button:hover {
    transform: translateY(-5px);
    cursor: pointer;
}

@keyframes clicked {
    0% {
        opacity: 1
    }
    50% {
        opacity: 0.65;
    }
    100% {
        opacity: 1
    }
}

.home_button:active {
    animation: clicked 0.4s ease;
}

@media (max-width: 670px) {
    .home_button {
        display: block;
        margin: 1rem auto;
    }
}

@media (max-width: 768px) {
    .muted_p {
        padding: 0 0rem;
    }
}

.text-display-home {
    width: 100vw;
    overflow: hidden;
    margin: 25px auto;
    color: rgb(85, 85, 85);
    font-size: 90%;
    cursor: pointer;
    border: 2px solid rgba(33, 33, 33, 0.74);
    border-right: none;
    border-left: none;
}

.text-display-home:hover {
    cursor: pointer;
    opacity: 0.7;
}