.nav_container{
    bottom: 0;
    margin: 0 auto;
    /* background-image: linear-gradient(0deg, #0c0c0c, #0c0c0ccc, rgba(0, 0, 0, 0)); */
    height: 6rem;
    transition: all 0.25s ease;
}
.nav_container:hover{
   cursor: pointer;
}
.nav_button{
    background-color: rgb(62, 62, 62);
    height: 3rem;
    width: 3rem;
    border-radius: 10px;
    margin: 0 auto;
    margin-top: 1.5rem;
    text-align: center;
    transition: all 0.25s ease;
    color: rgba(255, 255, 255, 0.6);
}

.nav_button:hover{
    color: rgba(255, 255, 255, 0.85);
    transform: translateY(-5px)
    /* background-color: rgba(255, 255, 255, 0.5); */
    /* box-shadow: 0 20px 20px rgba(0, 0, 0, 0.9); */
}

.nav_button i{
    margin-top: 1rem;
    transition: all 0.25s ease;
}

.nav_text {
    display: none;
    font-weight: 700;
    transition: all 0.25s ease;
    font-size: 0.9em;
    color: rgba(255, 255, 255, 0.6);
    text-decoration: none;
    margin: 0 auto;
    margin-top: 0.8rem;
}
.nav_arrow{
    width: 1.2rem;
    height: 1.2rem;
    margin: 0 auto;
    transition: all 0.25s ease;
    opacity: 0.8;
}
.nav_arrow:hover{
    cursor: pointer;
    opacity: 1;
    transform: translateY(-5px)
 }
.nav_arrow_cont{
    bottom: 10px;
    text-align: center;
    padding: 1rem;
    transition: all 0.25s ease;
    width: 10%;
    margin: 0 auto;
}
.hide_arrow{
    transition: all 0.25s ease;
    opacity: 0;
    transform: translateY(-100px);
}

.hide_nav{
    transition: all 0.25s ease;
    opacity: 0;
    transform: translateY(50px);
}

@keyframes clicked {
    0%   { opacity: 1}
    50% { opacity: 0.65;}
    100% {opacity: 1}
}

.nav_button:active, .nav_arrow:active{
    animation: clicked 0.4s ease;
}